import React from 'react'
import github  from "../Img/github-icon.png";

function Footer() {
	return (
		<>
		<footer className="footer bg-dark">
			<div>
				<a className='link-rs' href="https://rs.school/js/">RSSchool, 2021</a>
				<img className="github-icon" src={github} alt="github icon" />
				<a className="footer__link" href="https://github.com/vovantais" target='_blank'>vovantais</a>
			</div>
		</footer>
	</>
	)
}

export default Footer
