import React from 'react';
import main from "../Img/main.png";
import verify from "../Img/verify.png";
import mes from "../Img/mes.png";
import logIn from "../Img/logIn.png";
import error from "../Img/shc6.png";
import mesSuccess  from "../Img/success.png";
import home  from "../Img/home.png";
import income  from "../Img/income.png";
import expenses  from "../Img/expenses.png";
import sch1  from "../Img/sch1.png";
import Sch4  from "../Img/Sch4.png";
import sch2  from "../Img/sch2.png";
import sch3  from "../Img/sch3.png";
import settings  from "../Img/settings.png";
import proj  from "../Img/proj.png";
import state  from "../Img/state.png";
import examp  from "../Img/examp.png";
import front  from "../Img/front.png";
import commits  from "../Img/commits.png";

function About() {
	return (
		<div className='wrapper-about-app text-center'>
			<h1 className="about-app">About the App technical part</h1>
			<p>The application is written in React.js</p>
			<p>Server side uses Node.js</p>
			<p>MongoDB is used as a database.</p>
			<ul>
				<p>The application uses the following stack of technologies and libraries:</p>
				<li>• Redux</li>
				<li>• Styled-component</li>
				<li>• React-bootstrap</li>
				<li>• Json Web Token (JWT)</li>
				<li>• Express</li>
				<li>• Mongoose</li>
				<li>• Axios</li>
				<li>• React-toastify</li>
				<li>• Chart.js</li>
				<li>• Electron.js</li>
				<li>• Nodemailer</li>
			</ul>
			<p>In this photo you can see the structure of the project</p>
			<img  className="img-about" src={proj} alt="proj" />
			<p>Redax was used in the project here is its state</p>
			<img  className="img-about" src={state} alt="state" />
			<p>Server code example</p>
			<img  className="img-about" src={examp} alt="examp" />
			<p>Front code example</p>
			<img  className="img-about" src={front} alt="front" />
			<p>History Commits</p>
			<img  className="img-about" src={commits} alt="commits" />
			<p>When creating the project, there were difficulties with the implementation of work with charts,diagrams and data sorting, but they were successfully solved.</p>
				<p>The application server is hosted by Heroku, the project is built using electron.js and is available for installation on a PC. Also, the project is available in any browser.</p>
				<a href="https://youtu.be/F77yyg-m1cQ" target='_blank'>Link to the video presentation</a>
			<h1 className="about-app">About the App for Users</h1>
			<p >Hello everyone, on this page you can read the descriptions of the project. The project is called FBS (finance business success), the idea of the project is to keep track of your expenses and income, it will be useful to everyone who wants to keep track of their budget. In the application, you can see the history of your expenses and income for a different period of time, and you can also see your income and expenses for the last month in charts and graphs, read current business news, see currency rates and weather.Let's take a closer look</p>
			<p>1. On the first page you can see quotes from great people about finances.</p>
			<img  className="img-about" src={main} alt="mainPage" />
			<p>2. To enter the application, you need to register and verify your email.</p>
			<img  className="img-about" src={verify} alt="verify" />
			<p>That you keep during registration will receive a letter where there will be a confirmation key that will need to be entered for your pharification.</p>
			<img  className="img-about-data" src={mes} alt="messeage" />
			<p>3. When you pass the verification, you will be automatically directed to the application login page where you enter the data that you entered during registration. Also, if you forgot your password, you can click on the link to maintain your email address and you will receive a new password.</p>
			<img  className="img-about" src={logIn} alt="logIn" />
			<p>Congratulations, you have registered and entered the application.</p>
			<img  className="img-about-data" src={mesSuccess} alt="mesSuccess" />
			<p>You may see success messages or various errors if you fail.</p>
			<img  className="img-about-data" src={error} alt="error" />
			<p>4. Going into the application, you are taken to the home page where you can see the exchange rates, the weather for the city you entered during registration your name and time of day. Also great news from the world of finance and politics.</p>
			<img  className="img-about" src={home} alt="home" />
			<p>Then you can go to the income page and add your current income, salaries, bonuses, lottery winnings, and so on.</p>
			<img  className="img-about" src={income} alt="income" />
			<p>You can also add your expenses for different categories of clothing, entertainment, car and so on.</p>
			<img  className="img-about" src={expenses} alt="expenses" />
			<p>After that, you can go to the Schedule page where you will see several tables with expenses and incomes, as well as charts and charts for the current month with your expenses and incomes and the balance of the budget balance, the amount of wages and losses.</p>
			<img  className="img-about" src={sch1} alt="sch1" />
			<p>In the tables, you can make changes to the descriptions by clicking and after correcting the necessary information, the update button will appear.</p>
			<img  className="img-about-data" src={Sch4} alt="Sch4" />
			<p>You can view all information about your expenses and income in graphs and charts for the current month.</p>
			<img  className="img-about" src={sch2} alt="sch2" />
			<p>You can sort the data by month and year and see your expenses and income at different times and also compare them by month and year.</p>
			<img  className="img-about" src={sch3} alt="sch3" />
			<p>5. You also have a settings page where you can change your username, city and password and exit the application.</p>
			<img  className="img-about" src={settings} alt="settings" />
			<p>Thank you for using the application. Hope it meets your requirement.</p>
		</div>
	)
}

export default About;
